        },
        yAxis: {},
        series: series,
      };
      let option2 = {
        tooltip: {
          trigger: "axis",
        },
        legend: {
          data: ["折线1", "折线2"],
        },
        xAxis: {
          type: "category",
          data: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
        },
        yAxis: {
          type: "value",
        },
        series: [
          {
            name: "折线1",
            type: "line",
            data: [120, 132, 101, 134, 90, 230, 210],
          },
          {
            name: "折线2",
            type: "line",
            data: [220, 182, 191, 234, 290, 330, 310],
